'use client';

import { Menu, X, Newspaper, LogOut, LayoutDashboard } from 'lucide-react';
import Link from 'next/link';
import { Button } from '@/components/ui/button';
import { useState } from 'react';
import { Sheet, SheetContent, SheetTrigger, SheetClose } from '@/components/ui/sheet';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import { Avatar, AvatarFallback, AvatarImage } from '../ui/avatar';
import { useAuth } from '@/hooks/use-auth';


const navLinks = [
  { href: '/', label: 'Home' },
  { href: '/blog', label: 'Blog' },
  { href: '#about', label: 'About' },
  { href: '#how-it-works', label: 'How It Works' },
  { href: '#community', label: 'Community' },
];

export function Header() {
  const [isOpen, setIsOpen] = useState(false);
  const { user, loading, logout, userRole } = useAuth();

  return (
    <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
      <div className="container flex h-20 items-center justify-between">
        <Link href="/" className="font-headline text-2xl font-bold text-primary">
          Travonex
        </Link>
        
        <nav className="hidden md:flex items-center gap-6 text-sm font-medium">
          {navLinks.map(link => (
            <Link key={link.label} href={link.href} className="text-foreground/80 hover:text-primary transition-colors">
              {link.label}
            </Link>
          ))}
        </nav>

        <div className="hidden md:flex items-center gap-4">
           {loading ? (
                <div className="h-10 w-24 bg-muted rounded-2xl animate-pulse" />
           ) : user ? (
             <DropdownMenu>
                <DropdownMenuTrigger asChild>
                    <Button variant="ghost" className="relative h-10 w-10 rounded-full">
                        <Avatar>
                            <AvatarImage src={user.photoURL || undefined} alt={user.displayName || 'User'} />
                            <AvatarFallback>{user.displayName?.charAt(0) || user.email?.charAt(0)}</AvatarFallback>
                        </Avatar>
                    </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent className="w-56" align="end" forceMount>
                    <DropdownMenuLabel className="font-normal">
                    <div className="flex flex-col space-y-1">
                        <p className="text-sm font-medium leading-none">{user.displayName || user.email}</p>
                        <p className="text-xs leading-none text-muted-foreground">
                        {user.email}
                        </p>
                    </div>
                    </DropdownMenuLabel>
                    <DropdownMenuSeparator />
                    {userRole === 'admin' && (
                         <DropdownMenuItem asChild>
                            <Link href="/admin">
                                <LayoutDashboard className="mr-2 h-4 w-4" />
                                <span>Admin Dashboard</span>
                            </Link>
                        </DropdownMenuItem>
                    )}
                    <DropdownMenuItem asChild>
                       <Link href="/dashboard/submit">
                            <Newspaper className="mr-2 h-4 w-4" />
                            <span>Submit Story</span>
                        </Link>
                    </DropdownMenuItem>
                    <DropdownMenuSeparator />
                    <DropdownMenuItem onClick={() => logout()}>
                        <LogOut className="mr-2 h-4 w-4" />
                        <span>Log out</span>
                    </DropdownMenuItem>
                </DropdownMenuContent>
            </DropdownMenu>
           ): (
            <Button asChild className="rounded-2xl font-bold">
              <Link href="/login">Login</Link>
            </Button>
           )}
        </div>

        {/* Mobile Navigation */}
        <div className="md:hidden">
          <Sheet open={isOpen} onOpenChange={setIsOpen}>
            <SheetTrigger asChild>
              <Button variant="ghost" size="icon" aria-label="Open menu">
                <Menu className="h-6 w-6" />
              </Button>
            </SheetTrigger>
            <SheetContent side="right" className="w-full max-w-xs bg-background">
              <div className="flex flex-col h-full p-6">
                <div className="flex items-center justify-between mb-8">
                    <Link href="/" className="font-headline text-2xl font-bold text-primary" onClick={() => setIsOpen(false)}>
                      Travonex
                    </Link>
                    <SheetClose asChild>
                      <Button variant="ghost" size="icon" aria-label="Close menu">
                        <X className="h-6 w-6" />
                      </Button>
                    </SheetClose>
                </div>

                <nav className="flex flex-col gap-6 text-lg font-medium">
                  {navLinks.map(link => (
                    <SheetClose asChild key={link.label}>
                      <Link href={link.href} className="text-foreground/80 hover:text-primary transition-colors">
                        {link.label}
                      </Link>
                    </SheetClose>
                  ))}
                </nav>
                <div className="mt-auto border-t pt-6">
                   {user ? (
                     <div className="space-y-4">
                        <div className="flex items-center gap-3">
                             <Avatar>
                                <AvatarImage src={user.photoURL || undefined} alt={user.displayName || 'User'} />
                                <AvatarFallback>{user.displayName?.charAt(0) || user.email?.charAt(0)}</AvatarFallback>
                            </Avatar>
                            <div>
                                <p className="font-medium">{user.displayName || user.email}</p>
                                <p className="text-sm text-muted-foreground">{user.email}</p>
                            </div>
                        </div>
                         {userRole === 'admin' && (
                            <SheetClose asChild>
                                 <Link href="/admin" className="flex items-center w-full text-left p-2 rounded-md hover:bg-muted">
                                    <LayoutDashboard className="mr-2 h-5 w-5" />
                                    <span>Admin Dashboard</span>
                                </Link>
                            </SheetClose>
                        )}
                         <SheetClose asChild>
                            <Link href="/dashboard/submit" className="flex items-center w-full text-left p-2 rounded-md hover:bg-muted">
                                <Newspaper className="mr-2 h-5 w-5" />
                                <span>Submit Story</span>
                            </Link>
                        </SheetClose>
                        <Button onClick={() => logout()} variant="ghost" className="w-full justify-start p-2">
                             <LogOut className="mr-2 h-5 w-5" />
                            <span>Log out</span>
                        </Button>
                     </div>
                   ) : (
                     <SheetClose asChild>
                        <Button asChild className="w-full rounded-2xl font-bold">
                            <Link href="/login">Join / Login</Link>
                        </Button>
                    </SheetClose>
                   )}
                </div>
              </div>
            </SheetContent>
          </Sheet>
        </div>
      </div>
    </header>
  );
}
